/*
    @author:  Fitz
    @name:  patchZero
    @description:  8, 0
    @parms:  String 0
    @return:  String 8
*/

export default function (bin) {
    let bin_arr = bin.split('')
    let len = bin_arr.length

    if (len === 8) {
        return bin
    }
    while (len < 8) {
        bin_arr.unshift('0')
        len = bin_arr.length
    }
    return bin_arr.join('')
}
