<template>
    <div id="container">
        <div id="switch">
            <el-switch v-model="binOrdec" active-text="" active-color="#ff4949" inactive-text="">
            </el-switch>
        </div>

        <span>IP:</span>
        <el-input
            type="text"
            placeholder="ip"
            v-model="ip_address"
            :maxlength="binOrdec ? 35 : 15"
            show-word-limit
            :clearable="true"
            size="small"
            :style="{ marginLeft: '21px' }"
            ref="el_input"
        ></el-input>

        <br />

        <span style="margin-right: 20px;"> (): </span>
        <el-autocomplete
            class="inline-input"
            v-model="subnet_mask"
            :fetch-suggestions="suggestions"
            placeholder=""
            :maxlength="15"
            show-word-limit
            :clearable="true"
            size="small"
        ></el-autocomplete>

        <el-divider></el-divider>

        <div id="details">
            <el-descriptions :column="1" border>
                <el-descriptions-item
                    v-for="(item, index) in details"
                    :key="index"
                    :labelStyle="{ 'text-align': 'center', 'background-color': index%2===0?'#E1F3D8':'#DCDCDC', 'font-size': '14px', 'color': 'black' }"
                    :contentStyle="{ 'text-align': 'center', 'background-color': index%2===0?'#E1F3D8':'#DCDCDC', 'font-size': '14px', 'color': 'black' }"
                    :label="item.label"
                >
                    {{ item.result }}
                </el-descriptions-item>
            </el-descriptions>
        </div>
    </div>
</template>

<script>
    import { mapGetters } from 'vuex'
    import utils from '../utils'

    export default {
        name: 'IpDetail', // 
        data: function() {
            return {
                advices: [],
                details: [
                    {
                        label: "iP:",
                        result: null,
                    },
                    {
                        label: "iP:",
                        result: null
                    },
                    {
                        label: ":",
                        result: null
                    },
                    {
                        label: "(01):",
                        result: null
                    },
                    {
                        label: "(01):",
                        result: null
                    },
                    {
                        label: ":",
                        result: null
                    },
                    {
                        label: "(10):",
                        result: null
                    },
                    {
                        label: "(2):",
                        result: null
                    },
                    {
                        label: ":",
                        result: null
                    },
                    {
                        label: ":",
                        result: null
                    },
                    {
                        label: "(10):",
                        result: null
                    },
                    {
                        label: "(2):",
                        result: null
                    },
                    {
                        label: "()(10):",
                        result: null
                    },
                    {
                        label: "()(2):",
                        result: null
                    },
                ],
            }
        },
        mounted() {
            this.advices = this.loadAll()
        },
        methods: {
            // 
            suggestions(queryString, cb) {
                var advices = this.advices
                var results = queryString ? advices.filter(this.createFilter(queryString)) : advices
                //  callback 
                cb(results)
            },
            createFilter(queryString) {
                return restaurant => {
                    return restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0
                }
            },
            loadAll() {
                return [{ value: '255.0.0.0' }, { value: '255.255.0.0' }, { value: '255.255.255.0' }]
            },
            // 
        },
        computed: {

            ...mapGetters([
                "convert_ip_address",
                "ipClass",
                "bits_of_subnet",
                "num_of_subnet",
                "bits_of_hosts",
                "host_number_in_dec",
                "host_number_in_bin",
                "hosts",
                "useful_hosts",
                "network_address_dec",
                "network_address_bin",
                "host_address_dec",
                "host_address_bin",
            ]),

            ip_address: {
                get() {
                    return this.$store.state.ip_address
                },
                set(val) {
                    // el-input
                    if (val === '') {
                        this.$store.state.ip_address = val
                        return
                    }

                    const reg = /(\.|[0-9]){1}/gi
                    let str_len = val.length

                    if (reg.test(val[str_len - 1])) {
                        if (val.endsWith('.', str_len - 1) && val.endsWith('.')) return // . : 192..168
                        this.$store.state.ip_address = val
                    }

                    // , ip255
                    if (!this.binOrdec) {
                        utils.address_spliter(val).forEach(fragment => {
                            if (Number(fragment) > 255) {
                                this.$message.closeAll()
                                this.$message({
                                    showClose: true,
                                    message: 'ip !!! ',
                                    type: 'error',
                                })
                                return
                            }
                        })
                    }
                },
            },
            binOrdec: {
                get() {
                    return this.$store.state.binOrdec
                },
                set(val) {
                    this.$store.state.ip_address = ''
                    this.$store.state.binOrdec = val
                    if (val) {
                        this.$refs.el_input.$el.style.width = '330px'
                    } else {
                        this.$refs.el_input.$el.style.width = '200px'
                    }
                },
            },
            subnet_mask: {
                get() {
                    return this.$store.state.subnet_mask
                },
                set(val) {
                    // el-input
                    if (val === '') {
                        this.$store.state.subnet_mask = val
                        return
                    }

                    const reg = /(\.|[0-9]){1}/gi
                    let str_len = val.length

                    if (reg.test(val[str_len - 1])) {
                        if (val.endsWith('.', str_len - 1) && val.endsWith('.')) return // . : 192..168
                        this.$store.state.subnet_mask = val
                    }

                    // , ip255
                    if (!this.binOrdec) {
                        utils.address_spliter(val).forEach(fragment => {
                            if (Number(fragment) > 255) {
                                this.$message.closeAll()
                                this.$message({
                                    showClose: true,
                                    message: ' !!! ',
                                    type: 'error',
                                })
                                return
                            }
                        })
                    }
                },
            },

        },
        watch: {
            binOrdec: {
                immediate: true,
                handler(bool) {
                    this.$nextTick(() => {
                        if (bool) {
                            this.$refs.el_input.$el.style.width = '330px'
                            // , 
                            this.details[0].label = "iP:"
                        } else {
                            this.$refs.el_input.$el.style.width = '200px'
                            // , 
                            this.details[0].label = "iP:"
                        }
                    })
                },
            },
            convert_ip_address: {
                immediate: true,
                handler(val) {
                    this.details[0].result = val
                },
            },
            ipClass: {
                immediate: true,
                handler(c) {
                    this.details[1].result = c ? `${c}` : null
                }
            },
            bits_of_subnet: {
                immediate: true,
                handler(num) {
                    this.details[2].result = num
                }
            },
            num_of_subnet: {
                immediate: true,
                handler(obj) {
                    this.details[3].result = obj.old_router
                    this.details[4].result = obj.new_router
                }
            },
            bits_of_hosts: {
                immediate: true,
                handler(num) {
                    this.details[5].result = num
                }
            },
            host_number_in_dec: {
                immediate: true,
                handler(num) {
                    this.details[6].result = num
                }
            },
            host_number_in_bin: {
                immediate: true,
                handler(num) {
                    this.details[7].result = num
                }
            },
            hosts: {
                immediate: true,
                handler(num) {
                    this.details[8].result = num
                }
            },
            useful_hosts: {
                immediate: true,
                handler(num) {
                    this.details[9].result = num
                }
            },
            network_address_dec: {
                immediate: true,
                handler(address) {
                    this.details[10].result = address
                }
            },
            network_address_bin: {
                immediate: true,
                handler(address) {
                    this.details[11].result = address
                }
            },
            host_address_dec: {
                immediate: true,
                handler(address) {
                    this.details[12].result = address
                }
            },
            host_address_bin: {
                immediate: true,
                handler(address) {
                    this.details[13].result = address
                }
            },
        },
    }
</script>

<style lang="less" scoped>
    .el-input {
        margin: 20px 0;
    }

    #details {
        width: 65%;
        margin: 0 auto;
    }

    .el-descriptions-item_cell {
        background-color: red!important;
    }
</style>
