<template>
    <el-menu class="el-menu-demo" mode="horizontal" text-color="#409eff" active-text-color="#42b983" :default-active="default_active" :router="true">
        <el-menu-item
            v-for="nav in navs"
            :index="nav.index"
            :key="nav.index"
            :class="{ hello_page_style: nav.index === '/helloPage' }"
        >
            {{ nav.title }}
            <span style="color: red;">{{ nav.subtitle }}</span>
        </el-menu-item>
    </el-menu>
</template>

<script>
    export default {
        name: 'Nav', // 
        data() {
            return {
                navs: [
                    {
                        index: '/helloPage',
                        title: '',
                        subtitle: '',
                    },
                    {
                        index: '/ipClass',
                        title: 'IP',
                    },
                    {
                        index: '/ipDetails',
                        title: 'IP',
                        subtitle: '()',
                    },
                    {
                        index: '/isSameNet',
                        title: 'IP',
                        subtitle: '',
                    },
                    {
                        index: '/subnetting',
                        title: '',
                        subtitle: '',
                    },
                    {
                        index: '/netConfigGen',
                        title: '',
                        subtitle: '(...)',
                    },
                ],
                default_active: "",
            }
        },
        watch: {
            $route: {
                immediate: true,
                handler(cur_route) {
                    let index = cur_route.matched?.[0]?.path
                    if (index) {
                        this.default_active = index
                    }else {
                        this.$router.push({name: "HelloPage"})
                    }
                }
            }
        },
    }
</script>

<style lang="less" scoped>
    .el-menu {
        width: 95%;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    .el-menu-item {
        flex-grow: 1;
        flex-shrink: 0;
        font-weight: bold;
        font-size: 15px;
        &:first-of-type {
            font-size: 25px;
            position: relative;
            left: -30px;
            top: 0;
        }
    }

    .hello_page_style {
        color: black !important;
        border: none !important;
    }
</style>
