/*
    @author:  Fitz
    @name:  num_of_subnet
    @description:  
    @parms:  Number 
    @return:  Object   
*/

export default function (bits_of_subnet) {
    if (bits_of_subnet < 1) {
        return {
            old_router: 0,
            new_router: 0,
        }
    } else {
        return {
            old_router: 2 ** bits_of_subnet - 2,
            new_router: 2 ** bits_of_subnet,
        }
    }
}
