<template>
    <div>
        <div id="pics">
            <img src="../assets/ipclassifier.png" />
            <img src="../assets/ipclassifier2.png" />
        </div>

        <el-divider></el-divider>

        <div id="main">
            <div id="switch">
                <el-switch
                    v-model="binOrdec"
                    inactive-text=""
                    active-text=""
                    active-color="#ff4949"
                ></el-switch>
            </div>
            &nbsp;&nbsp;&nbsp;&nbsp;
            <label for="ip">ip: </label>
            <el-input
                id="ip"
                type="text"
                placeholder="IP"
                v-model="ip_address"
                :maxlength="maxlength"
                show-word-limit
                :clearable="true"
                ref="el_input"
            >
            </el-input>
            <div id="result" v-show="ipClass"> {{ ipClass }} </div>
        </div>
    </div>
</template>

<script>
    import { mapGetters } from "vuex"
    import utils from "../utils/index"

    export default {
        name: 'IpClass', // 
        data: function() {
            return {}
        },
        computed: {
            ...mapGetters([
                "ipClass"
            ]),

            ip_address: {
                get() {
                    return this.$store.state.ip_address
                },
                set(val) {
                    // el-input
                    if (val === "") {
                        this.$store.state.ip_address = val
                        return
                    }

                    const reg = /(\.|[0-9]){1}/ig
                    let str_len = val.length

                    if( reg.test(val[str_len-1]) ) {
                        if (val.endsWith(".", str_len-1) && val.endsWith(".")) return   // . : 192..168
                        this.$store.state.ip_address = val
                    }

                    // , ip255
                    if (!this.binOrdec) {
                        utils.address_spliter(val).forEach(
                            fragment => {
                                if (Number(fragment) > 255) {
                                    this.$message.closeAll()
                                    this.$message({
                                        showClose: true,
                                        message: 'ip !!! ',
                                        type: 'error'
                                    })
                                    return
                                }
                            }
                        )
                    }
                }
            },
            binOrdec: {
                get() {
                    return this.$store.state.binOrdec
                },
                set(val) {
                    this.$store.state.ip_address = ""
                    this.$store.state.binOrdec = val
                    if (val) {
                        this.$refs.el_input.$el.style.width = "330px"
                    }else {
                        this.$refs.el_input.$el.style.width = "200px"
                    }
                }
            },

            // ip, 
            maxlength() {
                return this.binOrdec? 35 : 15
            }
        },
        watch: {
            binOrdec: {
                immediate: true,
                handler(bool) {
                    this.$nextTick(
                        ()=> {
                            if (bool) {
                                this.$refs.el_input.$el.style.width = "330px"
                            }else {
                                this.$refs.el_input.$el.style.width = "200px"
                            }
                        }
                    )
                }
            }
        }
    }
</script>

<style lang="less" scoped>
    #pics {
        display: flex;
        justify-content: space-around;
        align-items: center;

        img {
            width: 49%;
            height: 350px;
        }
    }

    .el-input {
        width: 200px;
        margin: 20px 0;
    }
</style>
