/*
    @author:  Fitz
    @name:  standard_host_number
    @description:  IP
    @parms:  String ip
    @return:  Number 
*/

export default function (ipClass) {
    switch (ipClass) {
        case 'A':
            return 24
        case 'B':
            return 16
        case 'C':
            return 8
        default:
            return
    }
}
