/*
    @author:  Fitz
    @name:  one_to_two
    @description:  
    @parms:  Number    Array 
    @return:  Array
*/

export default function (num, arr) {
    let result = []
    while (arr.length > 0) {
        let v = arr.splice(0, num)
        result.push(v)
    }
    return result
}
