<template>
    <div>
        <div id="miss_subnet_type" v-if="subnet_type === '0'">
            <el-empty description="1"></el-empty>
            <el-button style="margin-top: 12px;" @click="pre"></el-button>
        </div>

        <div id="required_conditions" v-else>
            <div id="notice" v-if="subnet_type === '4'">
                <span>, </span>
                <br />
                <br />

                <span style="color: rgb(65,184,131); font-weight: bold; font-size: 17px;">
                    : 3, 16, [16, 16, 16]  [16, 12, 10]
                </span>

                <br />
                <br />
                <span style="color: red; font-weight: bold; font-size: 25px;"
                    >!!!</span
                >
            </div>

            <div class="conditions_group">
                <span>IP():</span>
                <el-input
                    type="text"
                    placeholder="ip"
                    v-model="ip_address"
                    :maxlength="15"
                    show-word-limit
                    :clearable="true"
                    size="small"
                    :style="{ marginLeft: '21px', width: '200px' }"
                    ref="el_input"
                ></el-input>
            </div>

            <div class="conditions_group" v-if="subnet_type === '1' || subnet_type === '2'">
                <span style="margin-right: 20px;"> (): </span>
                <el-autocomplete
                    class="inline-input"
                    v-model="subnet_mask"
                    :fetch-suggestions="suggestions"
                    placeholder=""
                    :maxlength="15"
                    show-word-limit
                    :clearable="true"
                    size="small"
                ></el-autocomplete>
            </div>

            <div class="conditions_group" v-else-if="subnet_type === '3'">
                <span>:</span>
                <el-input
                    type="text"
                    placeholder=""
                    v-model.number="required_subnets_num"
                    :maxlength="15"
                    show-word-limit
                    :clearable="true"
                    size="small"
                    :style="{ marginLeft: '21px', width: '200px' }"
                ></el-input>
            </div>

            <div class="conditions_group" v-else>
                <span>( : [6, 24, 16, 32, 29] ):</span>
                <el-input
                    type="text"
                    placeholder=""
                    v-model="hosts_num_in_each_subnet"
                    :clearable="true"
                    size="small"
                    :style="{ marginLeft: '21px', width: '250px' }"
                ></el-input>
            </div>

            <div class="conditions_group">
                <el-button style="margin-top: 12px;" @click="pre"></el-button>
                <el-button style="margin-top: 12px;" v-if="subnet_type" @click="next()"></el-button>
            </div>
        </div>
    </div>
</template>

<script>
    import utils from '../../utils'
    import { mapState } from 'vuex'

    export default {
        name: 'Process2', // 
        data: function() {
            return {}
        },
        computed: {
            ...mapState(['subnet_type', 'binOrdec']),

            ip_address: {
                get() {
                    return this.$store.state.ip_address
                },
                set(val) {
                    // el-input
                    if (val === '') {
                        this.$store.state.ip_address = val
                        return
                    }

                    const reg = /(\.|[0-9]){1}/gi
                    let str_len = val.length

                    if (reg.test(val[str_len - 1])) {
                        if (val.endsWith('.', str_len - 1) && val.endsWith('.')) return // . : 192..168
                        this.$store.state.ip_address = val
                    }

                    // , ip255
                    if (!this.binOrdec) {
                        utils.address_spliter(val).forEach(fragment => {
                            if (Number(fragment) > 255) {
                                this.$message.closeAll()
                                this.$message({
                                    showClose: true,
                                    message: 'ip !!! ',
                                    type: 'error',
                                })
                                return
                            }
                        })
                    }
                },
            },

            subnet_mask: {
                get() {
                    return this.$store.state.subnet_mask
                },
                set(val) {
                    // el-input
                    if (val === '') {
                        this.$store.state.subnet_mask = val
                        return
                    }

                    const reg = /(\.|[0-9]){1}/gi
                    let str_len = val.length

                    if (reg.test(val[str_len - 1])) {
                        if (val.endsWith('.', str_len - 1) && val.endsWith('.')) return // . : 192..168
                        this.$store.state.subnet_mask = val
                    }

                    // , ip255
                    if (!this.binOrdec) {
                        utils.address_spliter(val).forEach(fragment => {
                            if (Number(fragment) > 255) {
                                this.$message.closeAll()
                                this.$message({
                                    showClose: true,
                                    message: ' !!! ',
                                    type: 'error',
                                })
                                return
                            }
                        })
                    }
                },
            },

            required_subnets_num: {
                get() {
                    return this.$store.state.required_subnets_num
                },
                set(num) {
                    // 
                    if (num === '') {
                        this.$store.state.required_subnets_num = ''
                    } else if (num < 1) {
                        this.$store.state.required_subnets_num = 1
                        alert('1')
                    } else {
                        this.$store.state.required_subnets_num = num
                    }
                },
            },
            hosts_num_in_each_subnet: {
                get() {
                    return this.$store.state.hosts_num_in_each_subnet
                },
                set(hosts_num_arr) {
                    // 
                    if (hosts_num_arr === '') {
                        this.$store.state.hosts_num_in_each_subnet = ''
                    }else {
                        this.$store.state.hosts_num_in_each_subnet = hosts_num_arr
                    }
                },
            }
        },
        methods: {
            // 
            suggestions(queryString, cb) {
                var advices = this.advices
                var results = queryString ? advices.filter(this.createFilter(queryString)) : advices
                //  callback 
                cb(results)
            },
            createFilter(queryString) {
                return restaurant => {
                    return restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0
                }
            },
            loadAll() {
                return [{ value: '255.0.0.0' }, { value: '255.255.0.0' }, { value: '255.255.255.0' }]
            },
            // 

            pre() {
                // this.$store.state.active = 0     // subnetting.vueurl path, 
                this.$router.push({ name: 'Process1' })
            },
            next() {
                // this.$store.state.active = 2     // subnetting.vueurl path, 
                if (!this.ip_address || !this.required_subnets_num) {
                    alert('')
                    return
                }
                this.$router.push({ name: 'Process3' })
            },
        },
        mounted() {
            this.advices = this.loadAll()
        },
    }
</script>

<style lang="less" scoped>
    #required_conditions {
        .conditions_group {
            display: block;
            margin: 20px auto;
        }
    }

    #notice {
        margin: 50px auto;
        padding: 15px;
        border: 1px solid rgb(150, 145, 145);
        box-shadow: 7px -5px 10px 0px gray;
    }
</style>
